// src/app/components/chat/ChatEmptyState.tsx

"use client";

import { useState } from "react";
import ChatPromptControls from "./ChatPromptControls";
import ChatSuggestedPrompts from "./ChatSuggestedPrompts";
import ChatInput from "./ChatInput";

export default function ChatEmptyState() {
  const [role, setRole] = useState("Prosecutor");
  const [tone, setTone] = useState("Professional");
  const [input, setInput] = useState("");

  return (
    <div className="flex flex-1 flex-col items-center justify-center px-4 py-8 text-center text-white dark:text-white">
      <div className="w-full max-w-3xl space-y-6 rounded-2xl bg-zinc-100 dark:bg-zinc-900 p-6 shadow-xl">
        <h2 className="text-xl font-semibold text-zinc-800 dark:text-white">
          What legal question do you have in mind?
        </h2>

        {/* Prompt Controls */}
        <div className="flex flex-col sm:flex-row sm:items-center sm:justify-start gap-4 text-left">
          <div className="flex flex-col">
            <label className="text-sm text-zinc-500 dark:text-zinc-400 mb-1">AI Role:</label>
            <select
              value={role}
              onChange={(e) => setRole(e.target.value)}
              className="rounded-md border border-zinc-300 dark:border-zinc-700 bg-white dark:bg-zinc-800 px-3 py-2 text-sm text-zinc-800 dark:text-white"
            >
              <option>Prosecutor</option>
              <option>Defendant</option>
              <option>Judge</option>
            </select>
          </div>

          <div className="flex flex-col">
            <label className="text-sm text-zinc-500 dark:text-zinc-400 mb-1">AI Tone:</label>
            <select
              value={tone}
              onChange={(e) => setTone(e.target.value)}
              className="rounded-md border border-zinc-300 dark:border-zinc-700 bg-white dark:bg-zinc-800 px-3 py-2 text-sm text-zinc-800 dark:text-white"
            >
              <option>Professional</option>
              <option>Neutral</option>
              <option>Casual</option>
            </select>
          </div>
        </div>

        {/* Chat Input */}
        <div className="w-full">
          <ChatInput
            input={input}
            setInput={setInput}
            role={role}
            tone={tone}
            isEmptyState
          />
        </div>

        {/* Suggested Prompts */}
        <ChatSuggestedPrompts selectedRole={role} setInput={setInput} />
      </div>
    </div>
  );
}
